import React, { Component, Fragment } from 'react';
import '../../styles/loginmodal.css';

class Input extends Component {
    static defaultProps = {
        type: 'text',
        name: 'input',
        labelBefore: undefined,
        labelAfter: undefined,
        seperateLabel: true,
        className: undefined,
        placeholder: undefined,
    };

    constructor(props) {
        super(props);
        this.state = {
            value: ''
        }
        
        this.handleKeyPress = this.handleKeyPress.bind(this);
    }

    handleChange(event) {
        this.setState({
            value: event.target.value
        })
    }

    render() {
        const {type, name, id, labelBefore, labelAfter, className, placeholder} = this.props;
        const input = (
            <input value={this.state.value}
                type={type}
                name={name}
                onChange={this.handleChange} />
        );
        const inputAndLabel = (!id) ? (<label>{ (labelBefore) ? (<Fragment>{labelBefore} {input}</Fragment>) : (<Fragment>{input} {labelAfter}</Fragment>) }</label>) :
            (labelBefore) ? (<Fragment> <label for={id}>{labelBefore}</label> {input} </Fragment>) :
                (labelAfter) ? (<Fragment> {input} <label for={id}>{labelAfter}</label>  </Fragment>) :
                    {input};
        return (
            { inputAndLabel }
        );
    }
}

class LoginForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            active: 'login'
        }
        
        this.handleLoginClick = this.handleLoginClick.bind(this);
        this.handleRegisterClick = this.handleRegisterClick.bind(this);
    }

    handleLoginClick() {
        this.setState({
            active: 'login'
        });
    }

    handleRegisterClick() {
        this.setState({
            active: 'register'
        });
    }

    render() {
        const form = (this.state.active === 'login') ? 
            (
            <div className="login-form">
                <div className="login-options">
                    <button className="active">Đăng nhập</button>
                    <button onClick={this.handleRegisterClick}>Tạo tài khoản</button>
                </div>
                <form className="form">
                    <div className="row">
                        <label for="email">Email / SĐT</label>
                        <input type="email" name="email" placeholder="Nhập Email hoặc Số điện thoại" />
                    </div>
                    <div className="row">
                        <label for="password">Mật khẩu</label>
                        <input type="password" name="password" placeholder="Mật khẩu từ 6 đến 32 ký tự" />
                    </div>
                    <p className="forgot">Quên mật khẩu? Nhấn vào <a href="#">đây</a></p>
                    <div className="buttons-group">
                        <button className="normal-login">Đăng nhập</button>
                        <button className="api-login facebook">
                            <img src="" />
                            <span>Đăng nhập Bằng Facebook</span>
                        </button>
                        <button className="api-login google">
                            <img src="" />
                            <span>Đăng nhập Bằng Google</span>
                        </button>
                        <button className="api-login zalo">
                            <img src="" />
                            <span>Đăng nhập Bằng Zalo</span>
                        </button>
                    </div>
                </form>
            </div>
            ) :
            (
            <div className="register-form">
                <div className="login-options">
                    <button onClick={this.handleLoginClick}>Đăng nhập</button>
                    <button className="active">Tạo tài khoản</button>
                </div>
                <form className="form">
                    <div className="row">
                        <label for="name">Họ tên</label>
                        <input type="text" name="name" placeholder="Nhập họ tên" />
                    </div>
                    <div className="row">
                        <label for="phone">SĐT</label>
                        <input type="text" name="phone" placeholder="Nhập số điện thoại" />
                    </div>
                    <div className="row">
                        <label for="email">Email</label>
                        <input type="email" name="email" placeholder="Nhập email" />
                    </div>
                    <div className="row">
                        <label for="password">Mật khẩu</label>
                        <input type="password" name="password" placeholder="Nhập mật khẩu từ 6 đến 32 ký tự" />
                    </div>
                    <div className="row">
                        <label>Giới tính</label>
                        <div>
                            <label for="male"><input type="radio" name="gender" value="male" />Nam</label>
                            
                            <label for="female"><input type="radio" name="gender" value="female" />Nữ</label>
                        </div>
                        
                    </div>
                    <div className="row">
                        <label>Ngày sinh</label>
                        <div>
                            <input type="date" name="date" />
                        </div>
                    </div>
                    
                    <div className="register-group">
                        <input type="checkbox" name="notification" />
                        <label for="notification" className="forgot">Nhận các thông báo, khuyến mãi của Abe Shoes qua email</label>
                        <button className="normal-login">Tạo tài khoản</button>
                        <p>Khi bạn nhấn đăng ký, bạn đã đồng ý thực hiện mọi giao dịch mua bán theo <a href="#">điều kiện sử dụng và chính sách của Abe Shoes</a></p>
                    </div>
                </form>
            </div>
            )

        return (
            <Fragment>
                <div className="modal-header">
                    <h2>Đăng nhập</h2>
                    <p>Đăng nhập để theo dõi đơn hàng, lưu danh sách sản phẩm yêu thích, nhận nhiều ưu đãi hấp dẫn.</p>
                    <img src="assets/images/login.png" />
                </div>
                { form }
            </Fragment>
            
        );
    }
}

export default class LoginModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            active: false
        }
        this.handleClose = this.handleClose.bind(this);
    }

    handleClose() {
        let modal = document.getElementById('login');
        modal.style.visibility = 'hidden';

        document.getElementsByClassName('login-modal')[0].classList.remove('jo');
    }

    render() {
        const {text, type} = this.props;
        return(
            <Fragment>
                <div className="modal" id="login">
                    <div className="login-modal">
                        <button onClick={this.handleClose} className="close">&times;</button>
                        <LoginForm />
                    </div>
                </div>
            </Fragment>
        );
    }
}